.flex-container {
    margin: 15px 15px;
  }
  .flex-container .inline {
    width: 300px!important;
    margin: 10px 10px 10px 10px;
  }
  .flex-container .small {
    height: 20px!important;
    line-height: 20px!important;
  }
  .flex-container img{
      margin:0px auto;
      width: 170px;
      height: 170px;
      background-color:rgb(231, 105, 21);
      border-radius: 20px;
  }
